'use client'

import { useState, useEffect, useRef } from 'react'
import { useParams, useRouter } from 'next/navigation'
import Link from 'next/link'

// ==========================================
// TYPES
// ==========================================

interface ContentElement {
  id: string
  type: ElementType
  content: any
  position: { x: number; y: number }
  size: { width: number; height: number }
  style?: React.CSSProperties
}

type ElementType = 
  | 'heading'
  | 'text'
  | 'button'
  | 'image'
  | 'video'
  | 'form'
  | 'countdown'
  | 'testimonial'
  | 'pricing'
  | 'divider'
  | 'spacer'
  | 'icon'
  | 'social'
  | 'columns'
  | 'html'

interface StepData {
  id: string
  name: string
  type: string
  path: string
  content: {
    headline: string
    subheadline?: string
    buttonText?: string
    buttonColor?: string
    backgroundColor: string
    backgroundImage?: string
    sections: Section[]
  }
  seo: {
    title: string
    description: string
    keywords: string
  }
}

interface Section {
  id: string
  type: 'hero' | 'content' | 'cta' | 'testimonials' | 'faq' | 'footer'
  backgroundColor: string
  padding: string
  elements: ContentElement[]
}

