// ==========================================
// MAIN COMPONENT
// ==========================================

export default function StepEditorPage() {
  const params = useParams()
  const router = useRouter()
  const funnelId = params.funnelId as string
  const stepId = params.stepId as string
  
  const [stepData, setStepData] = useState<StepData | null>(null)
  const [loading, setLoading] = useState(true)
  const [selectedElement, setSelectedElement] = useState<string | null>(null)
  const [selectedSection, setSelectedSection] = useState<string | null>(null)
  const [showElementPanel, setShowElementPanel] = useState(true)
  const [showSettingsPanel, setShowSettingsPanel] = useState(true)
  const [viewMode, setViewMode] = useState<'desktop' | 'tablet' | 'mobile'>('desktop')
  const [isDragging, setIsDragging] = useState(false)
  const [hasUnsavedChanges, setHasUnsavedChanges] = useState(false)
  
  const editorRef = useRef<HTMLDivElement>(null)
  
  // Load step data
  useEffect(() => {
    const loadStepData = async () => {
      setLoading(true)
      await new Promise(resolve => setTimeout(resolve, 500))
      setStepData(getMockStepData(stepId))
      setLoading(false)
    }
    loadStepData()
  }, [stepId])
  
  // Add element to section
  const handleAddElement = (type: ElementType, sectionId: string) => {
    if (!stepData) return
    
    const newElement: ContentElement = {
      id: `el-${Date.now()}`,
      type,
      content: getDefaultContent(type),
      position: { x: 0, y: 0 },
      size: { width: 100, height: 50 },
      style: getDefaultStyle(type)
    }
    
    const updatedSections = stepData.content.sections.map(section => {
      if (section.id === sectionId) {
        return { ...section, elements: [...section.elements, newElement] }
      }
      return section
    })
    
    setStepData({
      ...stepData,
      content: { ...stepData.content, sections: updatedSections }
    })
    setSelectedElement(newElement.id)
    setHasUnsavedChanges(true)
  }
  
  // Delete element
  const handleDeleteElement = (elementId: string) => {
    if (!stepData || !confirm('Delete this element?')) return
    
    const updatedSections = stepData.content.sections.map(section => ({
      ...section,
      elements: section.elements.filter(el => el.id !== elementId)
    }))
    
    setStepData({
      ...stepData,
      content: { ...stepData.content, sections: updatedSections }
    })
    setSelectedElement(null)
    setHasUnsavedChanges(true)
  }
  
  // Update element
  const handleUpdateElement = (elementId: string, updates: Partial<ContentElement>) => {
    if (!stepData) return
    
    const updatedSections = stepData.content.sections.map(section => ({
      ...section,
      elements: section.elements.map(el => 
        el.id === elementId ? { ...el, ...updates } : el
      )
    }))
    
    setStepData({
      ...stepData,
      content: { ...stepData.content, sections: updatedSections }
    })
    setHasUnsavedChanges(true)
  }
  
  // Add section
  const handleAddSection = () => {
    if (!stepData) return
    
    const newSection: Section = {
      id: `section-${Date.now()}`,
      type: 'content',
      backgroundColor: '#FFFFFF',
      padding: '60px',
      elements: []
    }
    
    setStepData({
      ...stepData,
      content: {
        ...stepData.content,
        sections: [...stepData.content.sections, newSection]
      }
    })
    setSelectedSection(newSection.id)
    setHasUnsavedChanges(true)
  }
  
  // Delete section
  const handleDeleteSection = (sectionId: string) => {
    if (!stepData || !confirm('Delete this section and all its elements?')) return
    
    setStepData({
      ...stepData,
      content: {
        ...stepData.content,
        sections: stepData.content.sections.filter(s => s.id !== sectionId)
      }
    })
    setSelectedSection(null)
    setHasUnsavedChanges(true)
  }
  
  // Save changes
  const handleSave = () => {
    // In production, this would save to API
    alert('Changes saved!')
    setHasUnsavedChanges(false)
  }
  
  // Get default content for element type
  const getDefaultContent = (type: ElementType) => {
    switch (type) {
      case 'heading':
        return { text: 'New Heading', level: 2, align: 'left' }
      case 'text':
        return { text: 'Add your text here...', align: 'left' }
      case 'button':
        return { text: 'Click Here', url: '#', style: 'primary' }
      case 'image':
        return { src: '', alt: '', width: 100 }
      case 'video':
        return { src: '', type: 'youtube', autoplay: false }
      case 'form':
        return { fields: [{ name: 'email', type: 'email', placeholder: 'Enter email' }], buttonText: 'Submit' }
      case 'countdown':
        return { endDate: new Date(Date.now() + 7 * 24 * 60 * 60 * 1000).toISOString(), style: 'boxes' }
      case 'testimonial':
        return { quote: 'Add testimonial quote...', author: 'Author Name', title: 'Title' }
      case 'pricing':
        return { plans: [{ name: 'Basic', price: 29, features: ['Feature 1', 'Feature 2'] }] }
      case 'divider':
        return { style: 'solid', color: '#E5E7EB' }
      case 'spacer':
        return { height: 40 }
      case 'icon':
        return { name: 'star', size: 48, color: '#3B82F6' }
      case 'social':
        return { links: [{ platform: 'facebook', url: '#' }] }
      case 'columns':
        return { columns: 2, items: [] }
      case 'html':
        return { code: '<!-- Custom HTML -->' }
      default:
        return {}
    }
  }
  
  // Get default style for element type
  const getDefaultStyle = (type: ElementType): React.CSSProperties => {
    switch (type) {
      case 'heading':
        return { fontSize: '32px', fontWeight: 'bold', color: '#1F2937' }
      case 'text':
        return { fontSize: '16px', color: '#4B5563', lineHeight: '1.6' }
      case 'button':
        return { backgroundColor: '#3B82F6', color: '#FFFFFF', borderRadius: '8px', padding: '12px 24px' }
      default:
        return {}
    }
  }
  
  // Get viewport width based on view mode
  const getViewportWidth = () => {
    switch (viewMode) {
      case 'mobile': return '375px'
      case 'tablet': return '768px'
      default: return '100%'
    }
  }
  
  // Get selected element data
  const getSelectedElementData = (): ContentElement | null => {
    if (!stepData || !selectedElement) return null
    for (const section of stepData.content.sections) {
      const element = section.elements.find(el => el.id === selectedElement)
      if (element) return element
    }
    return null
  }
  
  if (loading) {
    return (
      <div className="flex items-center justify-center h-screen">
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600"></div>
      </div>
    )
  }
  
  if (!stepData) {
    return (
      <div className="text-center py-12">
        <p className="text-gray-500">Step not found</p>
      </div>
    )
  }
  
  const selectedElementData = getSelectedElementData()
  
  return (
    <div className="h-screen flex flex-col bg-gray-100">
      {/* Top Toolbar */}
      <div className="bg-white border-b border-gray-200 px-4 py-2 flex items-center justify-between flex-shrink-0">
        <div className="flex items-center gap-4">
          <Link
            href={`/sub-account/digital-hub/funnels/${funnelId}`}
            className="flex items-center gap-2 text-gray-600 hover:text-gray-900"
          >
            <svg className="w-5 h-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
            </svg>
            <span className="font-medium">Back to Funnel</span>
          </Link>
          <div className="h-6 w-px bg-gray-300"></div>
          <span className="font-semibold text-gray-900">{stepData.name}</span>
          {hasUnsavedChanges && (
            <span className="text-xs text-yellow-600 bg-yellow-100 px-2 py-0.5 rounded">Unsaved changes</span>
          )}
        </div>
        
        <div className="flex items-center gap-2">
          {/* View Mode Toggle */}
          <div className="flex items-center border border-gray-300 rounded-lg overflow-hidden">
            {(['desktop', 'tablet', 'mobile'] as const).map((mode) => (
              <button
                key={mode}
                onClick={() => setViewMode(mode)}
                className={`p-2 ${viewMode === mode ? 'bg-blue-50 text-blue-600' : 'bg-white text-gray-500 hover:bg-gray-50'}`}
                title={mode}
              >
                {mode === 'desktop' && (
                  <svg className="w-5 h-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9.75 17L9 20l-1 1h8l-1-1-.75-3M3 13h18M5 17h14a2 2 0 002-2V5a2 2 0 00-2-2H5a2 2 0 00-2 2v10a2 2 0 002 2z" />
                  </svg>
                )}
                {mode === 'tablet' && (
                  <svg className="w-5 h-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 18h.01M7 21h10a2 2 0 002-2V5a2 2 0 00-2-2H7a2 2 0 00-2 2v14a2 2 0 002 2z" />
                  </svg>
                )}
                {mode === 'mobile' && (
                  <svg className="w-5 h-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 18h.01M8 21h8a2 2 0 002-2V5a2 2 0 00-2-2H8a2 2 0 00-2 2v14a2 2 0 002 2z" />
                  </svg>
                )}
              </button>
            ))}
          </div>
          
          <div className="h-6 w-px bg-gray-300 mx-2"></div>
          
          <button
            onClick={() => setShowElementPanel(!showElementPanel)}
            className={`p-2 rounded-lg transition-colors ${showElementPanel ? 'bg-blue-50 text-blue-600' : 'text-gray-500 hover:bg-gray-100'}`}
            title="Toggle Elements Panel"
          >
            <svg className="w-5 h-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6a2 2 0 012-2h2a2 2 0 012 2v2a2 2 0 01-2 2H6a2 2 0 01-2-2V6zM14 6a2 2 0 012-2h2a2 2 0 012 2v2a2 2 0 01-2 2h-2a2 2 0 01-2-2V6zM4 16a2 2 0 012-2h2a2 2 0 012 2v2a2 2 0 01-2 2H6a2 2 0 01-2-2v-2zM14 16a2 2 0 012-2h2a2 2 0 012 2v2a2 2 0 01-2 2h-2a2 2 0 01-2-2v-2z" />
            </svg>
          </button>
          
          <button
            onClick={() => setShowSettingsPanel(!showSettingsPanel)}
            className={`p-2 rounded-lg transition-colors ${showSettingsPanel ? 'bg-blue-50 text-blue-600' : 'text-gray-500 hover:bg-gray-100'}`}
            title="Toggle Settings Panel"
          >
            <svg className="w-5 h-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M10.325 4.317c.426-1.756 2.924-1.756 3.35 0a1.724 1.724 0 002.573 1.066c1.543-.94 3.31.826 2.37 2.37a1.724 1.724 0 001.065 2.572c1.756.426 1.756 2.924 0 3.35a1.724 1.724 0 00-1.066 2.573c.94 1.543-.826 3.31-2.37 2.37a1.724 1.724 0 00-2.572 1.065c-.426 1.756-2.924 1.756-3.35 0a1.724 1.724 0 00-2.573-1.066c-1.543.94-3.31-.826-2.37-2.37a1.724 1.724 0 00-1.065-2.572c-1.756-.426-1.756-2.924 0-3.35a1.724 1.724 0 001.066-2.573c-.94-1.543.826-3.31 2.37-2.37.996.608 2.296.07 2.572-1.065z" />
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 12a3 3 0 11-6 0 3 3 0 016 0z" />
            </svg>
          </button>
          
          <div className="h-6 w-px bg-gray-300 mx-2"></div>
          
          <button className="px-3 py-2 text-gray-600 hover:bg-gray-100 rounded-lg transition-colors text-sm">
            Preview
          </button>
          
          <button 
            onClick={handleSave}
            className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors text-sm font-medium"
          >
            Save Changes
          </button>
        </div>
      </div>
      
      {/* Main Editor Area */}
      <div className="flex-1 flex overflow-hidden">
        {/* Elements Panel (Left) */}
        {showElementPanel && (
          <div className="w-64 bg-white border-r border-gray-200 flex flex-col flex-shrink-0">
            <div className="p-4 border-b border-gray-200">
              <h3 className="font-semibold text-gray-900">Elements</h3>
              <p className="text-xs text-gray-500 mt-1">Drag elements to canvas</p>
            </div>
            
            <div className="flex-1 overflow-y-auto p-3">
              {['Text', 'Media', 'Interactive', 'Advanced'].map((category) => (
                <div key={category} className="mb-4">
                  <h4 className="text-xs font-semibold text-gray-500 uppercase tracking-wider mb-2">{category}</h4>
                  <div className="grid grid-cols-2 gap-2">
                    {ELEMENT_TYPES.filter(el => el.category === category).map((element) => (
                      <button
                        key={element.type}
                        onClick={() => {
                          if (selectedSection) {
                            handleAddElement(element.type, selectedSection)
                          } else if (stepData.content.sections.length > 0) {
                            handleAddElement(element.type, stepData.content.sections[0].id)
                          } else {
                            alert('Add a section first')
                          }
                        }}
                        className="p-3 bg-gray-50 rounded-lg hover:bg-blue-50 hover:border-blue-200 border border-transparent transition-all text-center group"
                        draggable
                      >
                        <span className="text-lg block mb-1 group-hover:scale-110 transition-transform">{element.icon}</span>
                        <span className="text-xs text-gray-600">{element.name}</span>
                      </button>
                    ))}
                  </div>
                </div>
              ))}
            </div>
            
            {/* Add Section Button */}
            <div className="p-3 border-t border-gray-200">
              <button
                onClick={handleAddSection}
                className="w-full py-2 border-2 border-dashed border-gray-300 rounded-lg text-gray-500 hover:border-blue-500 hover:text-blue-600 hover:bg-blue-50 transition-all text-sm"
              >
                + Add Section
              </button>
            </div>
          </div>
        )}
        
        {/* Canvas Area (Center) */}
        <div className="flex-1 overflow-auto bg-gray-200 p-6" ref={editorRef}>
          <div 
            className="mx-auto bg-white shadow-lg transition-all duration-300"
            style={{ 
              width: getViewportWidth(),
              maxWidth: '100%',
              minHeight: '100%'
            }}
          >
            {stepData.content.sections.map((section, sectionIndex) => (
              <div
                key={section.id}
                onClick={(e) => {
                  e.stopPropagation()
                  setSelectedSection(section.id)
                  setSelectedElement(null)
                }}
                className={`relative group transition-all ${
                  selectedSection === section.id 
                    ? 'ring-2 ring-blue-500 ring-offset-2' 
                    : 'hover:ring-2 hover:ring-blue-300 hover:ring-offset-2'
                }`}
                style={{ 
                  backgroundColor: section.backgroundColor,
                  padding: section.padding
                }}
              >
                {/* Section Controls */}
                <div className={`absolute top-2 right-2 flex items-center gap-1 transition-opacity ${
                  selectedSection === section.id ? 'opacity-100' : 'opacity-0 group-hover:opacity-100'
                }`}>
                  <span className="px-2 py-1 bg-blue-600 text-white text-xs rounded">
                    Section {sectionIndex + 1}
                  </span>
                  <button
                    onClick={(e) => {
                      e.stopPropagation()
                      handleDeleteSection(section.id)
                    }}
                    className="p-1 bg-red-500 text-white rounded hover:bg-red-600"
                    title="Delete section"
                  >
                    <svg className="w-4 h-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                    </svg>
                  </button>
                </div>
                
                {/* Section Elements */}
                <div className="space-y-4">
                  {section.elements.map((element) => (
                    <div
                      key={element.id}
                      onClick={(e) => {
                        e.stopPropagation()
                        setSelectedElement(element.id)
                        setSelectedSection(section.id)
                      }}
                      className={`relative p-4 rounded transition-all cursor-pointer ${
                        selectedElement === element.id
                          ? 'ring-2 ring-blue-500 bg-blue-50/50'
                          : 'hover:ring-2 hover:ring-blue-300 hover:bg-blue-50/30'
                      }`}
                    >
                      {/* Element Render */}
                      {renderElement(element)}
                      
                      {/* Element Controls */}
                      {selectedElement === element.id && (
                        <div className="absolute -top-3 right-0 flex items-center gap-1 bg-white shadow-lg rounded-lg p-1">
                          <button className="p-1 hover:bg-gray-100 rounded" title="Move up">
                            <svg className="w-4 h-4 text-gray-600" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 15l7-7 7 7" />
                            </svg>
                          </button>
                          <button className="p-1 hover:bg-gray-100 rounded" title="Move down">
                            <svg className="w-4 h-4 text-gray-600" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
                            </svg>
                          </button>
                          <button className="p-1 hover:bg-gray-100 rounded" title="Duplicate">
                            <svg className="w-4 h-4 text-gray-600" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 16H6a2 2 0 01-2-2V6a2 2 0 012-2h8a2 2 0 012 2v2m-6 12h8a2 2 0 002-2v-8a2 2 0 00-2-2h-8a2 2 0 00-2 2v8a2 2 0 002 2z" />
                            </svg>
                          </button>
                          <button 
                            onClick={(e) => {
                              e.stopPropagation()
                              handleDeleteElement(element.id)
                            }}
                            className="p-1 hover:bg-red-100 rounded text-red-500" 
                            title="Delete"
                          >
                            <svg className="w-4 h-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16" />
                            </svg>
                          </button>
                        </div>
                      )}
                    </div>
                  ))}
                  
                  {section.elements.length === 0 && (
                    <div className="text-center py-12 text-gray-400 border-2 border-dashed border-gray-300 rounded-lg">
                      <p>Drop elements here</p>
                      <p className="text-sm mt-1">or click an element from the left panel</p>
                    </div>
                  )}
                </div>
              </div>
            ))}
            
            {stepData.content.sections.length === 0 && (
              <div className="text-center py-20 text-gray-400">
                <p className="text-lg mb-2">No sections yet</p>
                <p className="text-sm mb-4">Add a section to start building your page</p>
                <button
                  onClick={handleAddSection}
                  className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
                >
                  Add First Section
                </button>
              </div>
            )}
          </div>
        </div>
        
        {/* Settings Panel (Right) */}
        {showSettingsPanel && (
          <div className="w-80 bg-white border-l border-gray-200 flex flex-col flex-shrink-0">
            <div className="p-4 border-b border-gray-200">
              <h3 className="font-semibold text-gray-900">
                {selectedElementData ? 'Element Settings' : selectedSection ? 'Section Settings' : 'Page Settings'}
              </h3>
            </div>
            
            <div className="flex-1 overflow-y-auto p-4">
              {selectedElementData ? (
                <ElementSettings 
                  element={selectedElementData} 
                  onUpdate={(updates) => handleUpdateElement(selectedElementData.id, updates)}
                />
              ) : selectedSection ? (
                <SectionSettings 
                  section={stepData.content.sections.find(s => s.id === selectedSection)!}
                  onUpdate={(updates) => {
                    const updatedSections = stepData.content.sections.map(s =>
                      s.id === selectedSection ? { ...s, ...updates } : s
                    )
                    setStepData({
                      ...stepData,
                      content: { ...stepData.content, sections: updatedSections }
                    })
                    setHasUnsavedChanges(true)
                  }}
                />
              ) : (
                <PageSettings 
                  stepData={stepData}
                  onUpdate={(updates) => {
                    setStepData({ ...stepData, ...updates })
                    setHasUnsavedChanges(true)
                  }}
                />
              )}
            </div>
          </div>
        )}
      </div>
    </div>
  )
}

// ==========================================
// ELEMENT RENDERER
// ==========================================

function renderElement(element: ContentElement): React.ReactNode {
  switch (element.type) {
    case 'heading':
      const HeadingTag = `h${element.content.level || 2}` as keyof JSX.IntrinsicElements
      return (
        <HeadingTag 
          style={{ 
            textAlign: element.content.align || 'left',
            ...element.style 
          }}
        >
          {element.content.text}
        </HeadingTag>
      )
    
    case 'text':
      return (
        <p style={{ textAlign: element.content.align || 'left', ...element.style }}>
          {element.content.text}
        </p>
      )
    
    case 'button':
      return (
        <div style={{ textAlign: element.content.align || 'center' }}>
          <button style={{ ...element.style, cursor: 'pointer' }}>
            {element.content.text}
          </button>
        </div>
      )
    
    case 'image':
      return element.content.src ? (
        <img 
          src={element.content.src} 
          alt={element.content.alt || ''} 
          style={{ maxWidth: '100%', ...element.style }}
        />
      ) : (
        <div className="bg-gray-200 rounded-lg p-8 text-center text-gray-500">
          <span className="text-4xl mb-2 block">üñºÔ∏è</span>
          <p>Click to add image</p>
        </div>
      )
    
    case 'video':
      return element.content.src ? (
        <div className="aspect-video bg-black rounded-lg"></div>
      ) : (
        <div className="bg-gray-200 rounded-lg p-8 text-center text-gray-500 aspect-video flex items-center justify-center">
          <div>
            <span className="text-4xl mb-2 block">‚ñ∂Ô∏è</span>
            <p>Click to add video</p>
          </div>
        </div>
      )
    
    case 'form':
      return (
        <div className="max-w-md mx-auto">
          {element.content.fields?.map((field: any, idx: number) => (
            <input
              key={idx}
              type={field.type}
              placeholder={field.placeholder}
              className="w-full px-4 py-3 border border-gray-300 rounded-lg mb-3"
              readOnly
            />
          ))}
          <button 
            className="w-full py-3 text-white rounded-lg font-medium"
            style={{ backgroundColor: element.content.buttonColor || '#3B82F6' }}
          >
            {element.content.buttonText || 'Submit'}
          </button>
        </div>
      )
    
    case 'countdown':
      return (
        <div className="flex justify-center gap-4">
          {['Days', 'Hours', 'Mins', 'Secs'].map((unit) => (
            <div key={unit} className="bg-gray-900 text-white rounded-lg p-4 text-center min-w-[80px]">
              <div className="text-3xl font-bold">00</div>
              <div className="text-xs text-gray-400">{unit}</div>
            </div>
          ))}
        </div>
      )
    
    case 'testimonial':
      return (
        <div className="bg-gray-50 rounded-xl p-6 text-center">
          <p className="text-lg italic text-gray-700 mb-4">"{element.content.quote}"</p>
          <p className="font-semibold text-gray-900">{element.content.author}</p>
          <p className="text-sm text-gray-500">{element.content.title}</p>
        </div>
      )
    
    case 'columns':
      return (
        <div className={`grid gap-6`} style={{ gridTemplateColumns: `repeat(${element.content.columns || 3}, 1fr)` }}>
          {element.content.items?.map((item: any, idx: number) => (
            <div key={idx} className="text-center p-4">
              <span className="text-4xl mb-3 block">{item.icon}</span>
              <h3 className="font-semibold text-gray-900 mb-2">{item.title}</h3>
              <p className="text-gray-600 text-sm">{item.description}</p>
            </div>
          ))}
        </div>
      )
    
    case 'divider':
      return <hr style={{ borderColor: element.content.color || '#E5E7EB' }} />
    
    case 'spacer':
      return <div style={{ height: element.content.height || 40 }}></div>
    
    default:
      return (
        <div className="bg-gray-100 rounded-lg p-4 text-center text-gray-500">
          {element.type} element
        </div>
      )
  }
}

// ==========================================
// SETTINGS PANELS
// ==========================================

function ElementSettings({ element, onUpdate }: { element: ContentElement; onUpdate: (updates: Partial<ContentElement>) => void }) {
  return (
    <div className="space-y-4">
      <div>
        <label className="block text-sm font-medium text-gray-700 mb-1">Element Type</label>
        <p className="text-sm text-gray-500 capitalize">{element.type}</p>
      </div>
      
      {/* Content Settings based on type */}
      {(element.type === 'heading' || element.type === 'text') && (
        <>
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">Text Content</label>
            <textarea
              value={element.content.text || ''}
              onChange={(e) => onUpdate({ content: { ...element.content, text: e.target.value } })}
              rows={3}
              className="w-full px-3 py-2 border border-gray-300 rounded-lg text-sm"
            />
          </div>
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">Text Align</label>
            <select
              value={element.content.align || 'left'}
              onChange={(e) => onUpdate({ content: { ...element.content, align: e.target.value } })}
              className="w-full px-3 py-2 border border-gray-300 rounded-lg text-sm bg-white"
            >
              <option value="left">Left</option>
              <option value="center">Center</option>
              <option value="right">Right</option>
            </select>
          </div>
        </>
      )}
      
      {element.type === 'heading' && (
        <div>
          <label className="block text-sm font-medium text-gray-700 mb-1">Heading Level</label>
          <select
            value={element.content.level || 2}
            onChange={(e) => onUpdate({ content: { ...element.content, level: parseInt(e.target.value) } })}
            className="w-full px-3 py-2 border border-gray-300 rounded-lg text-sm bg-white"
          >
            <option value={1}>H1 - Main Heading</option>
            <option value={2}>H2 - Section Heading</option>
            <option value={3}>H3 - Subsection</option>
            <option value={4}>H4 - Small Heading</option>
          </select>
        </div>
      )}
      
      {element.type === 'button' && (
        <>
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">Button Text</label>
            <input
              type="text"
              value={element.content.text || ''}
              onChange={(e) => onUpdate({ content: { ...element.content, text: e.target.value } })}
              className="w-full px-3 py-2 border border-gray-300 rounded-lg text-sm"
            />
          </div>
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">Button URL</label>
            <input
              type="text"
              value={element.content.url || ''}
              onChange={(e) => onUpdate({ content: { ...element.content, url: e.target.value } })}
              className="w-full px-3 py-2 border border-gray-300 rounded-lg text-sm"
              placeholder="https://..."
            />
          </div>
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">Button Color</label>
            <input
              type="color"
              value={element.style?.backgroundColor as string || '#3B82F6'}
              onChange={(e) => onUpdate({ style: { ...element.style, backgroundColor: e.target.value } })}
              className="w-full h-10 border border-gray-300 rounded-lg cursor-pointer"
            />
          </div>
        </>
      )}
      
      {/* Style Settings */}
      <div className="pt-4 border-t border-gray-200">
        <h4 className="font-medium text-gray-900 mb-3">Style</h4>
        <div className="space-y-3">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">Font Size</label>
            <input
              type="text"
              value={element.style?.fontSize as string || '16px'}
              onChange={(e) => onUpdate({ style: { ...element.style, fontSize: e.target.value } })}
              className="w-full px-3 py-2 border border-gray-300 rounded-lg text-sm"
              placeholder="16px"
            />
          </div>
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">Text Color</label>
            <input
              type="color"
              value={element.style?.color as string || '#1F2937'}
              onChange={(e) => onUpdate({ style: { ...element.style, color: e.target.value } })}
              className="w-full h-10 border border-gray-300 rounded-lg cursor-pointer"
            />
          </div>
        </div>
      </div>
    </div>
  )
}

